const attrsWithoutUnit = ['font-weight', 'opacity', 'line-height'];

export function addDefaultUnit(theme:Record<string, string|number>):Record<string, string|number> {
  return Object.entries(theme).reduce((acc,cur) => {
    const [key, value] = cur;
    if(typeof value !== 'number' || attrsWithoutUnit.includes(key)) {
      return {
        ...acc,
        [key]: value
      }
    }
    return {
      ...acc,
      [key]: `${value}px`
    }
  },{})
}

export function transformThemeObjToCssString(theme:Record<string, string|number>){
  return Object.entries(addDefaultUnit(theme)).reduce((acc,cur) => {
    const [key, value] = cur;
    return `${acc}${key}:${value};`
  },'')
}
